// src/pages/MapPage.js
import React, { useState, useEffect, useContext } from "react";
import { Typography } from "@mui/material";
//Leaflet dependency
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  useMap,
} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import { MarkerContext } from "../context/MarkerContext"; // Context

//  ()
const gymIcon = new L.Icon({
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
  iconSize: [25, 41], // 
  iconAnchor: [12, 41], // 
  popupAnchor: [1, -34], // 
  shadowSize: [41, 41], // 
});

//  ()
const customUserIcon = new L.Icon({
  iconUrl: require("leaflet/dist/images/marker-icon-2x.png"), // 
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
  className: "my-green-marker",
});

// 
const SetMapCenter = ({ position }) => {
  const map = useMap(); // 
  useEffect(() => {
    if (position) {
      map.setView(position, 13); // 
    }
  }, [position, map]);

  return null;
};

const MapPage = () => {
  const { markers } = useContext(MarkerContext); // MarkerContext
  const [errorMessage, setErrorMessage] = useState(null);
  const [userLocation, setUserLocation] = useState(null); // 
  const [loadingLocation, setLoadingLocation] = useState(true); // 

  // 
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setUserLocation([
            position.coords.latitude,
            position.coords.longitude,
          ]);
          setLoadingLocation(false); // 
        },
        (error) => {
          console.error("Error fetching user's location: ", error);
          setErrorMessage("");
          setLoadingLocation(false); // 
        }
      );
    } else {
      setErrorMessage("");
    }
  }, []);

  // 
  // useEffect(() => {
  //   const geocodeAddress = async (address) => {
  //     if (!address) return; // 

  //     try {
  //       const response = await axios.get(
  //         "https://nominatim.openstreetmap.org/search",
  //         {
  //           params: {
  //             q: address,
  //             format: "json",
  //           },
  //         }
  //       );

  //       // 
  //       if (response.data.length > 0) {
  //         const { lat, lon } = response.data[0]; // 
  //         const newMarker = {
  //           position: [parseFloat(lat), parseFloat(lon)],
  //           address,
  //           type: "custom", // 
  //         };
  //         setMarkers((prevMarkers) => [...prevMarkers, newMarker]); // 
  //         setErrorMessage(null); // 
  //       } else {
  //         setErrorMessage("");
  //       }
  //     } catch (error) {
  //       console.error(error);
  //       setErrorMessage("");
  //     }
  //   };
  //   geocodeAddress(address); // 
  // }, [address]); //  address 

  if (loadingLocation) {
    return <Typography>...</Typography>;
  }

  return (
    <>
      {/*  */}
      {errorMessage && <p style={{ color: "red" }}>{errorMessage}</p>}

      {/*  */}
      <MapContainer
        center={userLocation || [22.9999, 120.227]} //
        zoom={13}
        style={{ height: "100vh", width: "100%" }}
      >
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />

        {/*  */}
        {userLocation && <SetMapCenter position={userLocation} />}
        {/*  */}
        {userLocation && (
          <Marker position={userLocation}>
            <Popup></Popup>
          </Marker>
        )}

        {/*  */}
        {markers.map((marker, index) => (
          <Marker
            key={index}
            position={marker.position}
            icon={marker.type === "default" ? gymIcon : customUserIcon} // 
          >
            <Popup>{marker.address}</Popup>
          </Marker>
        ))}
      </MapContainer>
    </>
  );
};

export default MapPage;
