import React, { useContext, useState } from "react";
import {
  TextField,
  Button,
  Checkbox,
  List,
  ListItem,
  ListItemText,
  IconButton,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Autocomplete,
  Fab,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import MapIcon from "@mui/icons-material/Map";
import AddIcon from "@mui/icons-material/Add"; // For the "+" symbol in the floating button
import { Link } from "react-router-dom";
import { LocalizationProvider } from "@mui/x-date-pickers";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import dayjs from "dayjs";
import axios from "axios";
import { MarkerContext } from "../context/MarkerContext";
import CustomDateTimePicker from "../components/CustomDateTimePicker";
import Layout from "../components/Layout"; 

//  isSameOrAfter dayjs  isSameOrAfter 
import isSameOrAfter from "dayjs/plugin/isSameOrAfter";
dayjs.extend(isSameOrAfter);

function ToDoList() {
  const [todos, setTodos] = useState(() => {
    //  Local Storage  todos
    const storedTodos = localStorage.getItem("todos");
    return storedTodos ? JSON.parse(storedTodos) : [];
  });
  const [task, setTask] = useState(""); //  task 
  const [date, setDate] = useState(dayjs());

  const [editIndex, setEditIndex] = useState(null);
  const [dateFilter, setDateFilter] = useState(dayjs()); //dayjs()dateFilterdateFilter
  const [selectedDate, setSelectedDate] = useState(dayjs()); // 
  const [openDialog, setOpenDialog] = useState(false); // 
  const [openDeleteDialog, setOpenDeleteDialog] = useState(false); // 
  const [deleteIndex, setDeleteIndex] = useState(null); // 
  const handleOpenDialog = () => setOpenDialog(true); // 
  const handleCloseDialog = () => setOpenDialog(false); // 
  const { addMarker } = useContext(MarkerContext); //  addMarker 
  const [address, setAddress] = useState(""); // 
  const { markers, getMarkerAddresses } = useContext(MarkerContext);
  const markerLocations = getMarkerAddresses(); //
  const [errorMessage, setErrorMessage] = useState(null);

  const handleOpenDeleteDialog = (index) => {
    setTask("");
    setDate(dayjs());
    setDeleteIndex(index);
    setOpenDeleteDialog(true);
  };
  const handleCloseDeleteDialog = () => {
    setOpenDeleteDialog(false);
    setEditIndex(null);
  };
  // 
  const taskOptions = [
    "",
    "",
    "",
    "",
    "",
  ];

  // 
  const handleToggle = (index) => {
    const updatedTodos = [...todos];
    updatedTodos[index].completed = !updatedTodos[index].completed;
    const sortedTodos = updatedTodos.sort((a, b) => a.completed - b.completed);
    setTodos(sortedTodos);
    localStorage.setItem("todos", JSON.stringify(sortedTodos));
  };

  const handleAddTodo = async () => {
    if (task.trim()) {
      const newTodo = { task, date, address, completed: false }; 

      if (editIndex !== null) {
        // 
        const updatedTodos = [...todos];
        updatedTodos[editIndex] = newTodo; // 
        setTodos(updatedTodos);
        localStorage.setItem("todos", JSON.stringify(updatedTodos)); //  Local Storage
        setEditIndex(null);
      } else {
        // 
        setTodos([...todos, newTodo]); // 
        localStorage.setItem("todos", JSON.stringify([...todos, newTodo])); //  Local Storage

        // 
        try {
          const response = await axios.get(
            "https://nominatim.openstreetmap.org/search",
            {
              params: {
                q: address,
                format: "json",
              },
            }
          );

          if (response.data.length > 0) {
            const { lat, lon } = response.data[0];
            const newMarker = {
              position: [parseFloat(lat), parseFloat(lon)],
              address,
            };
            addMarker(newMarker); // context addMarker ;
            setErrorMessage(null); // 
          } else {
            setErrorMessage("");
          }
        } catch (error) {
          console.error(error);
          setErrorMessage("");
        }
      }
      setTask(""); // 
      setDate(dayjs()); // 
      setAddress(""); // 
      handleCloseDialog(); // 
    }
  };

  const handleEditTodo = (index) => {
    setTask(todos[index].task);
    setDate(dayjs(todos[index].date)); // dayjs
    setAddress(todos[index].address);
    setEditIndex(index);
    handleOpenDialog(); // 
  };

  const handleDeleteTodo = (index) => {
    const updatedTodos = todos.filter((_, i) => i !== index);
    setTodos(updatedTodos);
    localStorage.setItem("todos", JSON.stringify(updatedTodos)); //  Local Storage
    handleCloseDeleteDialog();
  };

  //  dateFilter 
  const handleFilterTodos = () => {
    setDateFilter(selectedDate);
  };

  ///dateFilterfilteredTodos
  const filteredTodos = todos.filter((todos) =>
    dayjs(todos.date).isSameOrAfter(dateFilter)
  );

  //  filteredTodos
  console.log("Filtered Todos:", filteredTodos);

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs}>
      <Layout mt={8}>
        {/*  mt  */}
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center", // Center horizontally
            marginBottom: "16px",
          }}
        >
          <CustomDateTimePicker
            label=""
            value={selectedDate} //  selectedDate 
            onChange={(newValue) => setSelectedDate(newValue)} // 
            slots={{ textField: TextField }}
          />
          <Button
            variant="contained"
            style={{ marginLeft: "1rem" }}
            onClick={handleFilterTodos}
          >
            
          </Button>
        </div>

        {/* todos */}
        {todos.length === 0 && (
          <List>
            <ListItem>
              <Checkbox></Checkbox>
              <ListItemText
                primary=""
                secondary=": , :  Focus"
                style={{ color: "gray" }}
              />
            </ListItem>
          </List>
        )}

        {/*  */}
        <List>
          {filteredTodos.map((todos, index) => (
            <ListItem key={todos.task + todos.date}>
              {" "}
              {/*  key */}
              <Checkbox
                checked={todos.completed}
                onChange={() => handleToggle(index)}
              />
              <ListItemText
                primary={todos.task} // 
                secondary={`:
                  ${
                    todos.date
                      ? dayjs(todos.date).format("YYYY-MM-DD")
                      : "No date available"
                  },
                    : ${todos.address}
                `} //3 todo.date  undefined  TypeError 
                style={{
                  textDecoration: todos.completed ? "line-through" : "none",
                  color: todos.completed ? "gray" : "black",
                }}
              />
              <IconButton onClick={() => handleEditTodo(index)}>
                <EditIcon />
              </IconButton>
              <IconButton onClick={() => handleDeleteTodo(index)}>
                <DeleteIcon />
              </IconButton>
              {/*  */}
              <Link to={`/map`}>
                <IconButton>
                  <MapIcon />
                </IconButton>
              </Link>
            </ListItem>
          ))}
        </List>

        {/*  */}
        <Fab
          color="primary"
          aria-label="add"
          style={{
            position: "fixed",
            bottom: "5dvh",
            right: "45dvw",
          }}
          onClick={handleOpenDialog}
        >
          <AddIcon />
        </Fab>
        {/* 
        <Button variant="contained" onClick={handleOpenDialog}>
          Add Todo
        </Button> */}

        {/* / */}
        <Dialog open={openDialog} onClose={handleCloseDialog}>
          <DialogTitle>
            {editIndex !== null ? "" : ""}
          </DialogTitle>
          <DialogContent>
            {/*  Autocomplete  */}
            <Autocomplete
              freeSolo // 
              options={taskOptions} // 
              value={task}
              onChange={(event, newValue) => {
                setTask(newValue);
              }}
              onInputChange={(event, newInputValue) => {
                setTask(newInputValue); // 
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Task"
                  variant="outlined"
                  fullWidth
                />
              )}
            />
            {/*  */}
            <Autocomplete
              freeSolo
              options={[...markerLocations]} // 
              value={address}
              onChange={(event, newValue) => {
                setAddress(newValue);
              }}
              onInputChange={(event, newInputValue) => {
                setAddress(newInputValue);
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Enter Address"
                  variant="outlined"
                  fullWidth
                />
              )}
            />

            <CustomDateTimePicker
              label="Due Date"
              value={date}
              onChange={(newValue) => setDate(newValue)}
              fullWidth
              slots={{ textField: TextField }}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDialog}></Button>
            <Button onClick={handleAddTodo}>
              {editIndex !== null ? "Update" : "Add"}
            </Button>
          </DialogActions>
        </Dialog>

        {/*  */}
        <Dialog open={openDeleteDialog} onClose={handleCloseDeleteDialog}>
          <DialogTitle></DialogTitle>
          <DialogContent></DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDeleteDialog}></Button>
            <Button onClick={handleDeleteTodo}></Button>
          </DialogActions>
        </Dialog>
      </Layout>
    </LocalizationProvider>
  );
}

export default ToDoList;
